.solarSystem {
    transform-style: preserve-3d;
    pointer-events: none;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
}

.logo {
    color: white;
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    margin: auto;
    text-align: center;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 100;
    letter-spacing: 4px;
}

.menu {
    display: block;
    position: absolute;
    cursor: pointer;
    left: 90px;
    z-index: 2;
    color: white;
    & .preview {
        width: 30px;
        height: 30px;
        background: yellow;
        float: left;
        background-size: auto 100% !important;
        position: absolute;
        border-radius: 100px;
        box-shadow: 0 -13px 10px 2px black inset;
    }
    & .info {
        position: relative;
        left: 50px;
        top: 1px;
    }
    h2 {
        font-size: 11px;
        opacity: 0.4;
    }
    h3 {
        font-size: 8px;
        opacity: 0.3;
    }
}

/* 각 행성에 대한 스타일 */
.pluto .preview {
    background: url('https://s3-us-west-2.amazonaws.com/s.cdpn.io/217233/asd.jpeg') no-repeat;
}
.neptune .preview {
    background: url('https://img00.deviantart.net/f068/i/2017/165/b/c/neptune_texture_map_by_jcpag2010-dbcjcv5.png') no-repeat;
}
.uranus .preview {
    background: url('https://img00.deviantart.net/957c/i/2017/165/4/9/uranus_texture_map_by_jcpag2010-db7yjwb.png') no-repeat;
}
/* 추가된 행성들을 위해 동일한 형식으로 스타일 추가 */